import { Component } from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';

import { Facebook, FacebookLoginResponse } from '@ionic-native/facebook';

@IonicPage()
@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html'
})
export class WelcomePage {
  userData = null;
  

 
  constructor(public navCtrl: NavController, private facebook: Facebook) { }

  // REGULAR LOGIN
  login() {
    this.navCtrl.push('LoginPage');
  }
  
  // SOCIAL MEDIA LOGIN
  facelogin() {
    this.facebook.login(['email', 'public_profile']).then((response: FacebookLoginResponse) => {
      this.facebook.api('me?fields=id,name,email,first_name,picture.width(300).height(300)', []).then(profile => {
        this.userData = {email: profile['email'], name: profile['name'], picture: profile['picture']['data']['url']};
        this.navCtrl.push('TabsPage', this.userData);
      });
    }).catch(e => console.log('Error logging into Facebook', e));    
  }

  // SIGN UP
  signup() {
    this.navCtrl.push('SignupPage');
  }

}


