import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { ActionSheetController } from 'ionic-angular';
import { ProfilePage } from '../profile/profile';
import { CommentsPage } from '../comments/comments';

@IonicPage()
@Component({
  selector: 'page-the-post',
  templateUrl: 'the-post.html',
})
export class ThePostPage {
  postID: string;
  username: string;
  name: string;
  
  post: {};
  
  constructor(
  public navCtrl: NavController, 
  public navParams: NavParams,
  public actionSheetCtrl: ActionSheetController) {

    // IS SET WHEN CALLED VIA NAVCTRL.PUSH
    this.postID = this.navParams.get("postID");
    this.username = this.navParams.get("username");
    this.name = this.navParams.get("name");
    
    // USE postID TO MAKE [HTTP GET REQUEST]
    this.post = {
      id: this.postID,
      username: this.username,
      name: this.name,
      type: "picture",
      category: "Aquatic",
      challenge: "Water Assault",
      description: "Your scientists were so preoccupied with whether or not they could, that they didn\'t stop to think if they should.",
      location: "MatÃ£o-SP",
      views: 1234,
      likes: 558,
      dislikes: 1451,
      date: "05/12/2017"
    }
  }

  openModalMore(postID){
    let actionSheet = this.actionSheetCtrl.create({
      //title: 'Modify your album',
      buttons: [
       {
          text: 'Edit',
          icon: 'create',
          handler: () => {
            console.log(' clicked');
          }
        },{
          text: 'Share',
          icon: 'share-alt',
          handler: () => {
            console.log(' clicked');
          }
        },{
          text: 'Delete',
          icon: 'trash',
          handler: () => {
            console.log(' clicked');
          }
        },{
          text: 'Report...',
          icon: 'warning',
          //role: 'destructive',
          handler: () => {
            console.log('Report clicked');
          }
        },{
          text: 'Share on Messenger',
          icon: 'chatboxes',
          handler: () => {
            console.log('Share clicked');
          }
        },{
          text: 'Share on Whatsapp',
          icon: 'logo-whatsapp',
          //role: 'cancel',
          handler: () => {
            console.log('Share clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  openModalShare(postID){
    let actionSheet = this.actionSheetCtrl.create({
      //title: 'Modify your album',
      buttons: [
        {
          text: 'Share on Facebook',
          icon: 'logo-facebook',
          handler: () => {
          }
        },{
          text: 'Share on Twitter',
          icon: 'logo-twitter',
          handler: () => {
          }
        },{
          text: 'Share on Messenger',
          icon: 'chatboxes',
          handler: () => {
            console.log('Share clicked');
          }
        },{
          text: 'Share on Whatsapp',
          icon: 'logo-whatsapp',
          //role: 'cancel',
          handler: () => {
            console.log('Share clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  // PULLDOWN REFRESH
  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');
      refresher.complete();
    }, 1000);
  }


  // NAVIGATE
  goToComments(postID){
    this.navCtrl.push(CommentsPage, postID); 
  }

  goToProfile(username){
    this.navCtrl.push(ProfilePage, {"username": username});
  }

}



