import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-challenges-description',
  templateUrl: 'challenges-description.html',
})


export class ChallengesDescriptionPage {
  title: string = this.navParams.get('title');
  category: string = this.navParams.get('category');
  description: string = this.navParams.get('description');
  icon: string = this.navParams.get('icon');
  
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  // ionViewDidLoad() {
  //   console.log(this.title);
  // }

}



