import { Component, ViewChild } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { TranslateService } from '@ngx-translate/core';
import { Config, Nav, Platform } from 'ionic-angular';
import { LocalNotifications } from '@ionic-native/local-notifications';

import { FirstRunPage } from '../pages/pages';
import { Settings } from '../providers/providers';

import { Facebook } from '@ionic-native/facebook';


@Component({
  template: `<ion-menu [content]="content">
    <ion-header>
      <ion-toolbar>
        <ion-title>Pages</ion-title>
      </ion-toolbar>
    </ion-header>

    <ion-content>
      <ion-list>
        <button menuClose ion-item *ngFor="let p of pages" (click)="openPage(p)">
          {{p.title}}
        </button>

        <button menuClose ion-item (click)="logout()">
          Logout
        </button>
      </ion-list>
    </ion-content>

  </ion-menu>
  <ion-nav #content [root]="rootPage"></ion-nav>`
})
export class MyApp {
  rootPage = FirstRunPage;

  @ViewChild(Nav) nav: Nav;

  pages: any[] = [
    //{ title: 'Tutorial', component: 'TutorialPage' },
    { title: 'Home', component: 'TabsPage' },
    { title: 'Privacy Policy', component: 'PrivacyPage' },
    { title: 'Terms of Service', component: 'TermsPage' },
    { title: 'Settings', component: 'SettingsPage' }
  ]

  constructor(
    private translate: TranslateService, 
    platform: Platform, 
    settings: Settings, 
    private config: Config, 
    private statusBar: StatusBar, 
    private splashScreen: SplashScreen,
    private fb: Facebook,
    private localNotifications: LocalNotifications) {

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();


      // [LOCAL NOTIFICATION ATTEMPT]
      this.localNotifications.schedule({
        id: 1,
        title: "Defy is on fire!",
        text: 'O aplicativo estÃ¡ funcionando perfeitamente!',
        data: { myData: "Insert hidden data here" }
      });


    });
    this.initTranslate();


    // CHECK GLOBAL SETTINGS "localStorage-ish" IF USER IS LOGGED
    // if (settings.getValue('isLogged'))  //does not FUCKING WORK LIKE THIS!
    //   this.rootPage = MainPage;
}

  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('en');

    if (this.translate.getBrowserLang() !== undefined) {
      this.translate.use(this.translate.getBrowserLang());
    } else {
      this.translate.use('en'); // Set your language here
    }

    this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
      this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  logout(){
    this.fb.logout().then((data) => {
      console.log('SUCESSFULLY LOGGED OUT FROM FACEBOOK!');
      this.nav.setRoot('WelcomePage');
    }, (error) => {
      // CHANGE LATER
      console.log('FACEBOOK LOGOUT DID NOT WORKED!');
      this.nav.setRoot('WelcomePage');
    });
    
  }
}



