import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TitlesPage } from '../titles/titles';
import { ThePostPage } from '../the-post/the-post';
// import { Settings } from '../../providers/providers';
import { HttpClient } from '@angular/common/http';


@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
  profile_tabs: string = 'tab1';
  username: string = "y_lumberjack"; // SET BY DEFAULT LOGGED USER
  profile: {};
  posts: any[];

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    // private settings: Settings,
    private http: HttpClient) {

    // WHEN CLICKED ON A goToProfile(username)
    if (this.navParams.get('username'))
      this.username = this.navParams.get('username'); // IF CALLED, THEN ITS REPLACED (meaning, not MY page)
    // else
    //   console.log(settings.getValue('loggedUser'));
    // console.log(this.navParams.get('username'));
    // try {
    //   this.settings.load().then(() => {
    //     this.settings.getValue('isLogged').then(val => {
    //       console.log(val);
    //     });
    //   });
    // } catch (e) {
    //   console.log('Your web browser does not support storing settings locally. In Safari, the most common cause of this is using "Private Browsing Mode". Some settings may not save or some features may not work properly for you.');
    // } 

    this.http.get('https://defy-182116.appspot.com/user/'+this.username).subscribe(
      data => {
        console.log("SUCCESS: Sucessfully retrieved user profile from server.");
        this.profile = data;
      },
      err => {
        console.log("ERROR: Could not retrieve data from server.");
        this.profile = 
        {
          name: 'Diego T. Yamaguchi',
          username: 'y_lumberjack',
          views: '123',
          followers: '456',
          following: '789',
          badge: '3',
          about: 'Hello! Thanks for stopping by! This is a sample description, k bye',
          lv: '3',
          xp: '4',
          title: 'Game Master',
          titles: ['Savage', 'Mr. Tite', 'Filho do capeta', 'Satan himself', 'Peixinho', 'Pica das Galaxias', 'Game Master'],
          posts: ['11','12','13','14','15','16','17','11','12']
        }
      }
    );
  }

  // ionViewDidLoad() {
  //   console.log(this.username);
  // }


  goToTitles(titles, selected){
    let data = {"titles": titles, "selected": selected}
    this.navCtrl.push(TitlesPage, data);            
  }

  goToPost(postID, ownerUsername, ownerName){
    this.navCtrl.push(ThePostPage, {"postID": postID, "username": ownerUsername, "name": ownerName});        
  }




  // -------------------------------------------------------------------------------------------------- //
  // [ DISABLE ACCOUNT ]
  disableAccount(){
    this.http.put('https://defy-182116.appspot.com/user/disable', {'username': this.username}).subscribe(
      data => {
        console.log("SUCCESS: Sucessfully disabled account. The account will remain inactivated until you decide to log in again.");
        window.location.href = "";
      },
      err => {
        console.log("ERROR: Could not disable account.");
      }
    );
  }

  // [MAKE ACCOUNT PRIVATE]
  setPrivateAccount(){
    this.http.put('https://defy-182116.appspot.com/user/private', {'username': this.username, 'is_private': true}).subscribe(
      data => {
        console.log("SUCCESS: Sucessfully made account private.");
      },
      err => {
        console.log("ERROR: Could not make account private.");
      }
    );
  }

  // [MAKE ACCOUNT PUBLIC]
  setPublicAccount(){
    this.http.put('https://defy-182116.appspot.com/user/private', {'username': this.username, 'is_private':false}).subscribe(
      data => {
        console.log("SUCCESS: Sucessfully made account public.");
      },
      err => {
        console.log("ERROR: Could not make account public.");
      }
    );
  }


  // [EDIT ACCOUNT]
  editProfile(){
    //open modal with form

  }

  submitEditProfile(){
    
  }

  // -------------------------------------------------------------------------------------------------- //
  // PULLDOWN REFRESH
  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    setTimeout(() => {
      console.log('Async operation has ended');

      // REFRESH PAGE (later just make a AJAX append)
      this.navCtrl.setRoot(this.navCtrl.getActive().component); 

      refresher.complete();
    }, 1000);
  }
}



