import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ActionSheetController, AlertController } from 'ionic-angular';
import { ProfilePage } from '../profile/profile';
import { CommentsPage } from '../comments/comments';
import { HttpClient } from '@angular/common/http';



@IonicPage()
@Component({
  selector: 'page-timeline',
  templateUrl: 'timeline.html',
})
export class TimelinePage {
  posts: any;
  username: string = 'y_lumberjack';
  
    constructor(
      public navCtrl: NavController, 
      public navParams: NavParams, 
      public actionSheetCtrl: ActionSheetController,
      private alertCtrl: AlertController,
      private http: HttpClient) {


        // HTTP GET REQUEST
        this.http.get('https://defy-182116.appspot.com/posts/').subscribe(
          // SUCCESS
          data => {
            console.log("SUCCESS: Sucessfully retrieved posts from server.");
            this.posts = data;
            console.log(data);
          },
          // ERROR
          err => {
            console.log("ERROR: Could not retrieve data from server.");
            this.posts = [
              {
                _id: "11",
                username: "DonaldDuck64",
                name: "Donald Trump",
                type: "video",
                category: "Food",
                challenge: "Go to CapcomBar",
                description: "Wait a minute. Wait a minute, Doc. Uhhh... Are you telling me that you built a time machine... out of a DeLorean?! Whoa. This is heavy.",
                location: "Jaboticabal-SP",
                views: 78965,
                likes: 123,
                dislikes: 654,
                date: "10/10/2017"
              },
              {
                _id: "14",
                username: "y_lumberjack",
                name: "Diego T. Yamaguchi",
                type: "video",
                category: "Music",
                challenge: "Play Zelda!",
                description: "I face the unknown future, with a sense of hope. Because if a machine, a Terminator, can learn the value of human life, maybe we can too.",
                location: "Jaboticabal-SP",
                views: 1234,
                likes: 51,
                dislikes: 1651,
                date: "05/12/2017"
              },
              {
                _id: "15",
                username: "Rolao",
                name: "Dr. Steve Zaragoza",
                type: "picture",
                category: "Aquatic",
                challenge: "Water Assault",
                description: "Your scientists were so preoccupied with whether or not they could, that they didn\'t stop to think if they should.",
                location: "MatÃ£o-SP",
                views: 999999,
                likes: 558,
                dislikes: 1451,
                date: "05/12/2017"
              }
            ];
          }
        );  
    }

    ionViewDidLoad() {
      console.log(this.posts);
    }

    openModalMore(postID, description, location){
      let actionSheet = this.actionSheetCtrl.create({
        //title: 'Modify your album',
        buttons: [
         {
            text: 'Edit',
            icon: 'create',
            handler: () => {
              console.log('Edit clicked');
              this.editPostPrompt(postID, description, location);
            }
          },{
            text: 'Share',
            icon: 'share-alt',
            handler: () => {
              console.log('Share clicked');
            }
          },{
            text: 'Delete',
            icon: 'trash',
            handler: () => {
              console.log('Delete clicked');
              this.deletePostPrompt(postID);
            }
          },{
            text: 'Report...',
            icon: 'warning',
            //role: 'destructive',
            handler: () => {
              console.log('Report clicked');
            }
          },{
            text: 'Share on Messenger',
            icon: 'chatboxes',
            handler: () => {
              console.log('Share on Messenger clicked');
            }
          },{
            text: 'Share on Whatsapp',
            icon: 'logo-whatsapp',
            //role: 'cancel',
            handler: () => {
              console.log('Share on Whatsapp clicked');
            }
          }
        ]
      });
      actionSheet.present();
    }

    openModalShare(postID){
      let actionSheet = this.actionSheetCtrl.create({
        //title: 'Modify your album',
        buttons: [
          {
            text: 'Share on Facebook',
            icon: 'logo-facebook',
            handler: () => {
            }
          },{
            text: 'Share on Twitter',
            icon: 'logo-twitter',
            handler: () => {
            }
          },{
            text: 'Share on Messenger',
            icon: 'chatboxes',
            handler: () => {
              console.log('Share clicked');
            }
          },{
            text: 'Share on Whatsapp',
            icon: 'logo-whatsapp',
            //role: 'cancel',
            handler: () => {
              console.log('Share clicked');
            }
          }
        ]
      });
      actionSheet.present();
    }


    // [EDIT POST PROMPT]
    editPostPrompt(postID, description, location) {
      let alert = this.alertCtrl.create({
        title: 'Edit post',
        inputs: [
          {
            name: 'description',
            placeholder: 'Description',
            value: description
          },
          {
            name: 'location',
            placeholder: 'Location',
            value: location
          }
        ],
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            handler: data => {
              console.log('Cancel clicked');
            }
          },
          {
            text: 'Save',
            handler: data => {
              // HTTP: UPDATE POST
              this.http.put('https://defy-182116.appspot.com/posts/', {'_id': postID, 'description':data.description, 'location':data.location}).subscribe(
                data => {
                  console.log("SUCCESS: Sucessfully updated post.");
                  this.navCtrl.setRoot(this.navCtrl.getActive().component);                  
                },
                err => {
                  console.log("ERROR: Could not update post.");
                }
              );
            }
          }
        ]
      });
      alert.present();
    }
    // [DELETE POST PROMPT]
    deletePostPrompt(postID) {
      let alert = this.alertCtrl.create({
        title: 'Delete post?',
        message: 'Are you sure you wanna delete this post?',
        buttons: [
          {
            text: 'No',
            role: 'cancel',
            handler: () => {
              console.log('Cancel was clicked');
            }
          },
          {
            text: 'Yes',
            handler: () => {
              console.log('Yes was clicked');
              this.http.delete('https://defy-182116.appspot.com/posts/'+postID).subscribe(
                data => {
                  console.log("SUCCESS: Sucessfully deleted post.");
                  this.navCtrl.setRoot(this.navCtrl.getActive().component);                  
                },
                err => {
                  console.log("ERROR: Could not delete post.");
                }
              );
            }
          }
        ]
      });
      alert.present();
    }



    // [LIKE POST]
    likePost(postID){
      this.http.put('http://localhost:8080/like', {'id_post': postID, 'username': this.username, 'type': 'like'}).subscribe(
        data => {
          console.log("SUCCESS: Sucessfully liked post."); 
        },
        err => {
          console.log("ERROR: Could not like post.");
        }
      );
    }
    // [DISLIKE POST]
    dislikePost(postID){
      this.http.put('http://localhost:8080/like', {'id_post': postID, 'username': this.username, 'type': 'dislike'}).subscribe(
        data => {
          console.log("SUCCESS: Sucessfully disliked post."); 
        },
        err => {
          console.log("ERROR: Could not dislike post.");
        }
      );
    }

    // PULLDOWN REFRESH
    doRefresh(refresher) {
      console.log('Begin async operation', refresher);
  
      setTimeout(() => {
        console.log('Async operation has ended');

        // REFRESH PAGE (later just make a AJAX append)
        this.navCtrl.setRoot(this.navCtrl.getActive().component); 

        refresher.complete();
      }, 1000);
    }


    // NAVIGATE
    goToComments(postID){
      this.navCtrl.push(CommentsPage, postID); 
    }

    goToProfile(username){
      this.navCtrl.push(ProfilePage, {"username": username});    
    }
}




