import { Facebook } from '@ionic-native/facebook';
import { Items } from '../mocks/providers/items';
import { WelcomePage } from '../pages/welcome/welcome';
import { Settings } from '../providers/providers';
import { User } from '../providers/providers';
import { Api } from '../providers/providers';
import { MyApp } from './app.component';

// MY PAGES
import { TimelinePageModule } from '../pages/timeline/timeline.module';
import { ProfilePageModule } from '../pages/profile/profile.module';
import { SearchPageModule } from '../pages/search/search.module';
import { ChallengesPageModule } from '../pages/challenges/challenges.module';
import { ChallengesListPageModule } from '../pages/challenges-list/challenges-list.module';
import { ChallengesDescriptionPage } from '../pages/challenges-description/challenges-description';
import { TermsPageModule } from '../pages/terms/terms.module';
import { PrivacyPageModule } from '../pages/privacy/privacy.module';

import { SettingsPageModule } from '../pages/settings/settings.module';
import { TitlesPage } from '../pages/titles/titles';
import { ThePostPage } from '../pages/the-post/the-post';
import { ChatroomsPage } from '../pages/chatrooms/chatrooms';
import { ChatPage } from '../pages/chat/chat';
import { CommentsPage } from '../pages/comments/comments';
import { EditProfilePage } from '../pages/edit-profile/edit-profile';

